<template>
  <div class="my">
    <div class="my-icon">
      <div class="icon-box">
        <div class="icon-img">
          <img :src="profile.avatarUrl" alt="" />
        </div>
        <div class="nickname">{{ profile.nickname }}</div>
      </div>
    </div>
    <div class="like">
      <div class="link-box">
        <div class="link-icon">
          <img src="../../assets/img/shell.png" alt="" />
          <div class="name">云贝</div>
        </div>
        <div class="link-icon">
          <img src="../../assets/img/Buy.png" alt="" />
          <div class="name">已购单曲</div>
        </div>
        <div class="link-icon">
          <img src="../../assets/img/notify.png" alt="" />
          <div class="name">通知</div>
        </div>
        <div class="link-icon">
          <img src="../../assets/img/Comment.png" alt="" />
          <div class="name">评论</div>
        </div>
      </div>
      <div class="playliat">
        <div class="playliat-text">创建歌单</div>
        <div
          class="playlist-box"
          v-for="(item, index) in playList"
          :key="index"
        >
          <img :src="item.coverImgUrl" alt="" />
          <div class="playlist-right">
            <div class="playliat-title">{{ item.name }}</div>
            <div class="playlist-num">{{ item.trackCount }}首</div>
          </div>
        </div>
      </div>
      <div class="playliat">
        <div class="playliat-text">收藏歌单</div>
        <div class="playlist-box" v-for="(item, index) in collect" :key="index">
          <img :src="item.coverImgUrl" alt="" />
          <div class="playlist-right">
            <div class="playliat-title">{{ item.name }}</div>
            <div class="playlist-num">{{ item.trackCount }}首</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Toast } from 'vant';
export default {
  data () {
    return {
      profile: '',
      playList: [],
      collect: []
    }
  },
  mounted () {
    let profile = JSON.parse(localStorage.getItem('profile'))
    this.profile = profile
    if (localStorage.getItem('userId') != '') {
      this.playlist()
    }
  },
  methods: {
    playlist () {
      let data = {
        uid: localStorage.getItem('userId')
      }
      this.api.playlist(data).then(res => {
        if (res.data.code == 200) {
          res.data.playlist.forEach(item => {
            if (item.subscribedCount == 0 || item.subscribedCount == 1) {
              this.playList.push(item)
            } else if (item.subscribedCount != 0 || item.subscribedCount != 1) {
              this.collect.push(item)
            }
          });
        } else {
          Toast(res.data.msg)
        }
      }).catch(err => {
        Toast(err)
      })
    }
  }
}
</script>

<style lang="less" scoped>
.my {
  position: relative;
  .my-icon {
    height: 516px;
    background: #ee2b2b;
    border-radius: 0 0 36px 36px;

    .icon-box {
      display: flex;
      align-content: center;
      justify-content: center;
      flex-wrap: wrap;
      height: 516px;

      .icon-img {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;

        img {
          width: 121px;
          height: 121px;
          object-fit: cover;
          border-radius: 50%;
          border: 8px solid #fff;
        }
      }
      .nickname {
        width: 100%;
        font-size: 48px;
        font-family: Source Han Sans CN;
        font-weight: 500;
        color: #ffffff;
      }
    }
  }
  .like {
    width: 93%;
    padding: 0 24px 0 24px;
    // height: 150px;
    position: absolute;
    top: 80%;
    margin-bottom: 168px;

    .link-box {
      width: 100%;
      height: 150px;
      background: #fff;
      border-radius: 12px;
      box-shadow: 0px 4px 8px 0px rgba(0, 0, 0, 0.05);
      display: flex;
      align-items: center;
      justify-content: space-evenly;

      .link-icon {
        width: 120px;
        height: 120px;
        img {
          width: 50px;
          height: 50px;
          object-fit: cover;
        }
        .name {
          font-size: 24px;
          color: rgba(0, 0, 0, 0.7);
        }
      }
    }

    .playliat {
      width: 100%;
      margin-top: 30px;
      background: #fff;
      border-radius: 12px;
      box-shadow: 4px 4px 8px 4px rgba(0, 0, 0, 0.05);

      .playliat-text {
        // width: 100%;
        font-size: 24px;
        text-align: left;
        padding: 20px 20px;
      }

      .playlist-box {
        padding: 0px 20px 20px 20px;
        display: flex;

        img {
          width: 60px;
          height: 60px;
          object-fit: cover;
          border-radius: 10px;
        }

        .playlist-right {
          margin-left: 20px;

          .playliat-title {
            font-size: 24px;
          }
          .playlist-num {
            font-size: 20px;
            color: rgba(0, 0, 0, 0.5);
            text-align: left;
          }
        }
      }
    }
  }
}
</style>